.inputRadio input {
    z-index: -1;
    opacity: 0;
    margin: 10px 0 0 7px;
}
.inputText {
    position: relative;
    padding: 0 0 0 35px;
    cursor: pointer;
}
.inputText:before {
    content: '';
    position: absolute;
    top: -3px;
    left: 0;
    width: 22px;
    height: 22px;
    border: 1px solid #CDD1DA;
    border-radius: 50%;
    background: #FFF;
}
.inputText:after {
    content: '';
    position: absolute;
    top: 1px;
    left: 4px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #9FD468;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
    opacity: 0;
    transition: .2s;
}
.inputRadio input:checked + .inputText:after {
    opacity: 1;
}
.inputRadio input:focus + .inputText:before {
    box-shadow: 0 0 0 3px rgba(255,255,0,.7);
}